.todo-app {
  border: 1px solid #ccc;
  padding: 10px;
  max-width: 400px;
  margin: auto;
  backdrop-filter: blur(20px);
}

.todo-input {
  display: flex;
  margin-bottom: 10px;
  color: aliceblue;
}

.todo-input input {
  flex-grow: 1;
  margin-right: 10px;
  background: transparent;
  color: aliceblue;
  border: none;
  outline: none;
  transition: border 0.3s ease;
}

.todo-input input:hover {
  border: none;
}

.todo-list {
  list-style: none;
  padding: 0;
  color: aliceblue;
}

.todo-list li {
  display: flex;
  justify-content: space-between;
  border: 1px solid #ccc;
  padding: 8px;
  margin-bottom: 5px;
}

body {
  height: 100vh;
  width: 100vw;
  z-index: -1;
  background-image: url('https://www.phind.com/_next/image?url=https%3A%2F%2Fimages.unsplash.com%2Fphoto-1557683316-973673baf926%3Fq%3D80%26w%3D1000%26auto%3Dformat%26fit%3Dcrop%26ixlib%3Drb-4.0.3%26ixid%3DM3wxMjA3fDB8MHxleHBsb3JlLWZlZWR8OHx8fGVufDB8fHx8fA%253D%253D&w=1920&q=75');
  backdrop-filter: blur(20px);
  background-color: rgba(255, 255, 255, 0.575);
  background-size: cover;
  overflow-y: hidden;
  overflow-x: hidden;
}

.todo_text {
  display: flex;
  justify-content: center;
  color: aliceblue;
}

.remove-button {
  cursor: pointer;
  background: transparent;
  border: none;
  color: aliceblue;
}

.add-button {
  cursor: pointer;
  background: transparent;
  border: none;
  color: aliceblue;
}